import React from 'react';
import './App.css';
import axios from './axios';
class Alltasks extends React.Component {
    constructor() {
        super()
        this.state = { tasklogs: [] }
    }

    componentDidMount() {

        axios.get("http://192.168.56.101:8000/tasks/")

            .then(response => response.data)
            .then(data => {
                this.setState({ tasklogs: data.results })
            })
    }

    render() {
        return (
            <div>
                <h1>Tasks List</h1>
                <ul>
                    {this.state.tasklogs.map(tasks => {
                        return <li key={`movie-${tasks.id}`}>Task id:{tasks.id} User Created:{tasks.usercreation}
                        Name: {tasks.name}</li>
                    })}
                </ul>
            </div>
        )
    }
}
export default Alltasks;
