import React from 'react';
import './App.css';
import axios from './axios';
class MyComponent extends React.Component {
  constructor() {
    super()
    this.state = { tasklogs: [] }
  }

  componentDidMount() {

    axios.get("http://192.168.56.101:8000/tasks/notif/")

      .then(response => response.data)
      .then(data => {
        this.setState({ tasklogs: data.results })
      })
  }

  render() {
    return (
      <div>
        <h1>Notifications List</h1>
        <ul>
          {this.state.tasklogs.map(notif => {
            return <li key={`movie-${notif.id}`}>Notification id:{notif.id} User_trigger:{notif.user_trigger}</li>
          })}
        </ul>
      </div>
    )
  }
}
export default MyComponent;
