import React, {Component} from "react";
import axios from './axios';

class Login extends Component {
    state = {
        data: [],
        loaded: false,
        placeholder: "Loading...",
        token: null
    };

    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        const data1 = new FormData(event.target);

        axios.post('http://192.168.56.101:8000/login/', data1
        )
            .then(data => {
                this.setState({data: data.data.info});
                if (data.data.info === 'Welcome')
                    localStorage.setItem('token',data.data.token);
                    this.setState({token: data.data.token});
                return data;
            })
    };


    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <label htmlFor="username">Enter username</label>
                <input id="username" name="username" type="text"/>
                <label htmlFor="password">Enter your password</label>
                <input id="password" name="password" type="password"/>
                <button>Send data!</button><br></br>
                {this.state.data}
            </form>

        );
    }
}

export default Login;