import React, {Component} from "react";
import axios from './axios';

class Registration extends Component {
    state = {
        data: [],
        loaded: false,
        placeholder: "Loading..."
    };

    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        const data = new FormData(event.target);
        axios.post('http://192.168.56.101:8000/signup/', data
        )
            .then(data => {
                console.log(data);
                console.log(data.data);
                this.setState({data: data.data.info});
                return data;
            })
    };

    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <label htmlFor="username">Enter username           </label>
                <input id="username" name="username" type="text"/><br></br>
                <label htmlFor="password">Enter your password      </label>
                <input id="password1" name="password1" type="password"/><br></br>
                <label htmlFor="password">Enter your password again</label>
                <input id="password2" name="password2" type="password"/><br></br>
                <button>Send data!</button><br></br>
                {this.state.data}
            </form>

        );
    }
}

export default Registration;