import React from 'react'
import ReactDOM from 'react-dom'
import { Route, BrowserRouter as Router } from 'react-router-dom'
import './index.css'
import Notifications from './notifread'
import * as serviceWorker from './serviceWorker';
import Login from './login'
import Registration from './Registration'
import Longest from './longest'
import Timerstart from './timerstart'
import Loglastmonth from './loglastmonth'
import Timerstop from './timerstop'
import Tasks from './tasks'
import Logout from './logout'
import MyTasks from "./mytasks";
import Timelog from "./timelog";
import TaskComplete from "./taskcomplete";
import CommentIt from './comment'
import Alltasks from "./alltasks";
import CreateTask from "./createtask";
const routing = (
    <Router>
        <div>
            <Route exact path="/" component={Login} />
            <Route path='/logout' component={Logout} />
            <Route path="/signup" component={Registration} />
            <Route path='/alltasks' component={Alltasks} />
            <Route path='/createtask' component={CreateTask} />
            <Route path='/tasks/:handle' component={Tasks} />
            <Route path='/tasks/:handle' component={CommentIt} />
            <Route path='/mytasks' component={MyTasks} />
            <Route path='/mytasks' component={TaskComplete} />
            <Route path="/notif" component={Notifications} />
            <Route path='/timelog/:handle' component={Timelog} />
            <Route path="/loglastmonth" component={Loglastmonth} />
            <Route path="/longest" component={Longest} />
            <Route path="/timer" component={Timerstart} />
            <Route path="/timer" component={Timerstop} />
        </div>
    </Router>
)

ReactDOM.render(routing, document.getElementById('root'));
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
