import React, {Component} from "react";
import axios from './axios';
class CommentIt extends Component {
    state = {
        data: [],
        loaded: false,
        placeholder: "Loading..."
    };

    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        const data = new FormData(event.target);
        const { handle } = this.props.match.params

        axios.post(`http://192.168.56.101:8000/tasks/${handle}/comment/`,data)
            .then(data => {
                console.log(data);
                console.log(data.data);
                this.setState({data: data.data.info});
                return data;
            })
    };
    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <label htmlFor="task">Comments </label>
                <input id="text" name="text" type="text" /><br></br>
                <button>Comment Task</button><br></br>
                {this.state.data}

            </form>

        );
    }
}

export default CommentIt;
