import React, {Component} from "react";
import axios from './axios';
class CreateTask extends Component {
    state = {
        data: [],
        loaded: false,
        placeholder: "Loading..."
    };

    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        const data = new FormData(event.target);

        axios.post(`http://192.168.56.101:8000/tasks/add/`,data)
            .then(data => {
                console.log(data);
                console.log(data.data);
                this.setState({data: data.data.info});
                return data;
            })
    };
    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <label htmlFor="task">Task creation </label><br></br>
                Name: <input id="name" name="name" type="text" /><br></br>
                Deadline DateTime: <input id="date_deadline" name="date_deadline" type="datetime"/><br></br>
                <button>Create Task</button><br></br>
                {this.state.data}

            </form>

        );
    }
}

export default CreateTask;