import React, {Component} from "react";
import axios from './axios';
class MyTasks extends Component {
    state = {
        data: []
    };
    componentDidMount () {


        axios.get(`http://192.168.56.101:8000/tasks/me/`)
            .then(response => response.data)

            .then(data => {
                this.setState({ data: data })
            }) }

    render() {
        return(
            <div>
                <h1>Task </h1>
                <ul>
                    {this.state.data.map(task => {
                        return <li>Task id:{task.id} Created by user:{task.usercreation} Name: {task.name}
                            <br></br>Deadline: {task.date_deadline} <br></br>Completed:{String(task.completed)}
                            {console.log(task.completed)}</li>

                    })}
                </ul>
            </div>
        );
    }
}
export default MyTasks;