import React, {Component} from "react";
import axios from './axios';
class Timelog extends Component {
    state = {
        data: [],
        total_time: 0
    };
    componentDidMount () {
        const { handle } = this.props.match.params

        axios.get(`http://192.168.56.101:8000/tasks/${handle}/timelogs`)
            .then(response => response.data)

            .then(data => {
                this.setState({total_time: data.results.total_time})
                this.setState({ data: data.results.list })
            }) }

    render() {
        return(
            <div>
                <h1>Task </h1>
                <ul>
                    <h3>Total time: {this.state.total_time}</h3>
                    {this.state.data.map(timelog => {
                        return <li>Timelog id:{timelog.id},User: {timelog.user}
                            Time Active: {timelog.time_active}</li>
                    })}
                </ul>
            </div>
        );
    }
}
export default Timelog;