import React from 'react';
import './App.css';
import axios from './axios';

class Loglastmonth extends React.Component {
    constructor() {
        super();
        this.state = {
            tasklogs: [],
            total_time: ''
        }
    }

    componentDidMount() {

        axios.get("http://192.168.56.101:8000/tasks/timelogmonth")

            .then(response => response.data)
            .then(data => {
                this.setState({
                    tasklogs: data.results.list,
                    total_time: data.results.total_time
                })
            })
    }

    render() {
        return (
            <div>
                <h1>Tasklogs List for last month</h1>
                <ul>
                    <h3>Total time for last month:{Math.trunc(this.state.total_time)} minutes</h3>
                    {this.state.tasklogs.map(taskslog => {
                        return <li key={`Tasklog-${taskslog.id}`}>Tasklog
                            id:{taskslog.id} Duration:{Math.trunc(taskslog.time_active)}
                            minutes</li>
                    })}
                </ul>
            </div>
        )
    }
}

export default Loglastmonth;
