import React from 'react';
import './App.css';
import axios from './axios';
class Longest extends React.Component {
    constructor() {
        super()
        this.state = { tasklogs: [] }
    }

    componentDidMount() {

        axios.get("http://192.168.56.101:8000/tasks/longest")

            .then(response => response.data)
            .then(data => {
                this.setState({ tasklogs: data })
            })
    }

    render() {
        return (
            <div>
                <h1>Longset Tasklogs List</h1>
                <ul>
                    {this.state.tasklogs.map(tasklog => {
                        return <li key={`tasklog-${tasklog.id}`}>Tasklog id:{tasklog.id} Duration:{tasklog.duration}</li>
                    })}
                </ul>
            </div>
        )
    }
}
export default Longest;
